@media only screen and (max-width: 450px){

    /*Removes the browser nav bar*/
    .navlinks{
      display: none;
    }
  
    /*Creates a nav bar hidden in the right side of the screen. Code from https://codepen.io/robdongas/pen/MWvKMRp*/
    .menu{
      position: absolute;
      right: -60vw;
      z-index: 998;
      color: black;
      background: #0B6E4F;
      width: 60vw;
      padding-top: 75px;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      border-style: solid;
      border-width: 2px;
      border-color: black;
      transition-property: right;
      transition-duration: 0.2s;
    }
  
    /*Makes the menu appear when the user clicks on it*/
    .menu.active{
      right: 0;
    }
  
    /*Styles the text/links inside the nav menu*/
    .menu a{
      color: white;
      font-size: 1.3em;
      text-decoration: none;
    }
  
    /*Creates the space where the hamburger menu would appear*/
    .menu-toggle{
      position: absolute;
      top: 5vh;
      right: 5vw;
      z-index: 999;
      height: 5vh;
      width: 8vw;
      outline: none;
      cursor: pointer;
      display: flex;
      align-items: center;
    }
  
    /*Styles the three bars of the hamburger menu*/
    .menu-toggle span, .menu-toggle span::before, .menu-toggle span::after{
      position: absolute;
      content: "";
      width: 8vw;
      height: 0.5vh;
      background: white;
      border-radius: 20px;
      transition: 500ms cubic-bezier(0.77, 0, 0.175, 1);
    }
  
    /*Spaces the top bar of the hamburger menu*/
    .menu-toggle span::before{
      top:-1.7vh;
    }
  
    /*Spaces the bottom bar of the hamburger menu*/
    .menu-toggle span::after{
      top: 1.7vh;
    }
  
    /*Makes the nav icon invisible when clicked*/
    .menu-toggle.active > span{
      background: transparent;
    }
  
    /*Colors the bars after they've been clicked*/
    .menu-toggle.active > span::before, .menu-toggle.active > span::after{
      top: 0px;
    }
  
    /*Rotates the top bar to form half of the "X"*/
    .menu-toggle.active > span::before{
      transform: rotate(-225deg);
    }
  
    /*Rotates the bottom bar to form the other half of the "X"*/
    .menu-toggle.active > span::after{
      transform: rotate(225deg);
    }
  
    /*Creates the invidual boxes inside the nav menu*/
    .navbox{
      margin-bottom: -2px; /*Makes it so the borders overlap and don't seem thicker*/
      background-color: #0B6E4F;
      width: 100%;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-style: solid;
      border-width: 2px 0px;
      border-color: white;
      transition-property: background-color;
      transition-duration: 0.2s;
    }
  
    /*Changes what the navboxes looks like when the user hovers over them*/
    .navbox:hover{
      background-color: #EBA640;
      color: black;
    }
  
    .header a{
      font-size: 0em;
    }
    
